<template>
	<div id="new-employee">
		<h3>New Employee</h3>
		<div class="row">
			<form @submit.prevent="saveEmployee">

				<div class="row">
					<div class="input-field col s12">
						<input type="text" v-model="employee_id">
						<label>Employee ID#</label>
					</div>
				</div>
				<div class="row">
					<div class="input-field col s12">
						<input type="text" v-model="name">
						<label>Name</label>
					</div>
				</div>
				<div class="row">
					<div class="input-field col s12">
						<input type="text" v-model="dept">
						<label>Department</label>
					</div>
				</div>
				<div class="row">
					<div class="input-field col s12">
						<input type="text" v-model="position">
						<label>Position</label>
					</div>
				</div>

				<button type="submit" class="btn orange">Submit</button>
				<router-link to="/" class="btn grey">Cancel</router-link>

			</form>
		</div>
	</div>
</template>

<script>

	import db from './firebaseInit'

	export default {
		name: 'new-employee',
		data() {
			return {
				employee_id: null,
				name: null,
				dept: null,
				position: null
			}
		},
		methods: {
			saveEmployee() {
				db.collection('employees').add({
					employee_id: this.employee_id,
					name: this.name,
					dept: this.dept,
					position: this.position
				}).then(docRef => {
					this.$router.push('/')
				}).catch(error => {
					console.log(err)
				})
			}
		}
	}
</script>


